"use client";
import { useEffect, useState } from "react";
import { databases, databaseId, studentsCollectionId } from "@/app/lib/appwrite";

const StudentPage = () => {
  const [students, setStudents] = useState<any[]>([]);

  const fetchStudents = async () => {
    const res = await databases.listDocuments(databaseId, studentsCollectionId);
    setStudents(res.documents);
  };

  useEffect(() => {
    fetchStudents();
  }, []);

  return (
    <div className="w-full min-h-screen p-4 sm:p-6 lg:p-8 bg-gray-50">
      <h1 className="text-xl sm:text-2xl font-bold mb-6 text-gray-800">
        Students
      </h1>

      <div className="bg-white rounded-lg shadow divide-y">
        {students.length === 0 && (
          <p className="p-4 text-gray-500">No students found.</p>
        )}
        {students.map((s) => (
          <div
            key={s.$id}
            className="flex flex-col sm:flex-row sm:justify-between sm:items-center p-4"
          >
            <span className="font-medium text-gray-800">
              {s.name} <span className="text-gray-500 text-sm">({s.class})</span>
            </span>
            <span className="mt-2 sm:mt-0 text-sm text-gray-600">
              Age: {s.age}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default StudentPage;
